import { CSSProperties } from "react";
import styled, { css } from "styled-components";

import { colors, sizes, spacingMap } from "../../styleguide";

export interface StyledRouteContentProps {
	zIndex?: CSSProperties["zIndex"];
	hasLocalNav?: boolean;
	contentSize?: "withLocalNav" | "fullScreen" | "withoutLocalNav" | "fullScreenWithMenuBar"
}

const fullScreenStyles = css`
	width: 100%;
	left: 0;
	height: 100%;
	top: 0;
	background-color: ${colors.white};
`;

const withLocalNavStyles = css`
	width: calc(100% - ${sizes.allNavigationsWidth});
	left: ${sizes.allNavigationsWidth};
`;

const fullScreenWithMenuBarStyles = css`
	width: 100%;
	left: 0;
	padding: ${spacingMap.lg};
	height: calc(100% - ${sizes.menuBarHeight});
	top: ${sizes.menuBarHeight};
`;


export const StyledRouteContent = styled.div<StyledRouteContentProps>`
	box-sizing: border-box;
	padding: ${spacingMap.md} ${spacingMap.md} 0 ${spacingMap.md};
	overflow: auto;
	position: fixed;
	${({ zIndex }) => zIndex && `
		z-index: ${zIndex};
	`}

	width: calc(100% - ${sizes.globalNavigationWidth});
	left: ${sizes.globalNavigationWidth};
	height: calc(100% - ${sizes.menuBarHeightWithSpacing});
	top: ${sizes.menuBarHeightWithSpacing};
	background-color: transparent;

	${({ contentSize }) => contentSize === "fullScreen" && fullScreenStyles};
	${({ contentSize }) => contentSize === "fullScreenWithMenuBar" && fullScreenWithMenuBarStyles};
	${({ contentSize, hasLocalNav }) => (contentSize === "withLocalNav" || hasLocalNav) && withLocalNavStyles};
`;
